import React, { useEffect, useState } from 'react';
import DhammaItem from '../dhamma/dhamma-item';
import { Dhamma } from '../dhamma/model';
import { Message } from '../message/message';
import { createStyles, Theme, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { from } from 'rxjs';
import { map } from 'rxjs/operators'
import axios from 'axios';
import { endianness } from 'os';

export interface DhammaListProp {
  startIndex?: number,
  endIndex?: number
}

export interface DhammaListState {
  error: boolean,
  dhammas: Dhamma[]
}

const useStyles = makeStyles((theme: Theme) => 
  createStyles({
    mainContent: {
      minHeight: 'calc(100vh - 14rem)',
      display: 'flex',
      justifyContent: 'space-around',
      flexWrap: 'wrap'
    },
    dhammaList: {
      width: '100%',
      display: 'flex',
      flexWrap: 'wrap'
    },
    header: {
      marginBottom: '2rem'
    }
  })
);

const GET_ALL_DHAMMAS = "https://www.mraukoomeditationcenter.org/all-dhammas.json";

const DhammaList: React.FC<DhammaListProp> = ({ startIndex, endIndex }) => {

  const classes = useStyles();
  const [ state, setState ] = useState<DhammaListState>();

  if(startIndex != null && endIndex != null && startIndex > endIndex) {
    startIndex = undefined;
    endIndex = undefined;
  }

  useEffect(() => {
    const subscription = from(axios.get(GET_ALL_DHAMMAS))
      .pipe(
        map(response => response),
        map(response => response.data),
      ).subscribe((dhammas: Dhamma[]) => {
        startIndex = 1;
        endIndex = 10;
        if(startIndex != null && endIndex != null) {
          dhammas = dhammas.slice(startIndex, endIndex);
        }
        setState({
          error: false,
          dhammas: dhammas
        });
      }, (error) => {
        setState({
            error: true,
            dhammas: []
        });
      });
      return () => {
        subscription.unsubscribe();
      };
  }, []);

  if(!state) {
    return (<div className={classes.mainContent}><Message type="Loading" /></div>)
  }

  const { error, dhammas } = state;

  if(error) {
    return (<Message type="Error" />)
  }

  return (
    <div>
      <Typography variant="h6" className={classes.header}>Dhamma</Typography>
      <div className={classes.dhammaList}>
        {
          dhammas.map((dhamma, index) => (
            <DhammaItem key={index} id={index} title={dhamma.title} />
          ))
        }
      </div>
    </div>
  );
}

export default DhammaList;